import * as QRCode from "qrcode.react";
import * as React from "react";
import * as CopyToClipboard from "react-copy-to-clipboard";

declare class UIOutDivProps {
    public password: string | undefined;
    public startGeneration: () => void;
    public generating: boolean;
    public canGenerate: boolean;
}

export class UIOutDiv extends React.Component {
    // @ts-ignore
    public props: UIOutDivProps;

    public render(): JSX.Element | undefined {
        return (<>
            <div className="outdiv" id="result">
                <div>
                    <span
                        onClick={this.props.startGeneration}>
                        {this.renderPasswordField()}
                    </span>
                    {this.renderCopyButton()}
                </div>
                {this.renderQRCode()}
            </div>
        </>);
    }

    private renderCopyButton(): JSX.Element | undefined {
        if (this.props.password) {
            return (
                <CopyToClipboard text={this.props.password}>
                    <button className="copy_button">Copy</button>
                </CopyToClipboard>);
        }
        return;
    }

    private renderPasswordField(): string {
        if (this.props.generating) {
            return "Generating...";
        }
        if (this.props.password) {
            return this.props.password;
        }
        if (this.props.canGenerate) {
            return "Generate!";
        }
        return "";
    }

    private renderQRCode(): JSX.Element | undefined {
        if (this.props.password) {
            return <QRCode value={this.props.password} />;
        }
        return undefined;
    }
}